import React, {PureComponent} from "react";
import PropTypes from "prop-types";
import ModalService from "eshop/flux/utils/OraModalService";
import OfferDetailsModal from "eshop/modules/cart/components/entriesList/shared/productDetails/OfferDetailsModal";

class OfferPropositionDetails extends PureComponent {
    static propTypes = {
        linkName: PropTypes.string,
        proposition: PropTypes.object,
        transactions: PropTypes.object
    };

    componentDidMount() {
      this.registerModalServiceSelectedPropositionDetails();
    };

    registerModalServiceSelectedPropositionDetails() {

        const proposition = this.props.proposition;
        if (proposition) {
            ModalService.add((data) => {
                return (
                    <OfferDetailsModal
                        id={"offer_proposition_details_" + proposition.code}
                        header={"Szczegóły"}
                        broadband={proposition.broadband}
                        tv={proposition.tvProduct}
                        tvPackages={proposition.tvPackages}
                        voip={proposition.voip}
                        voice={proposition.voice}
                        data={proposition.data}
                        transactions={this.props.transactions}
                    />)
            });
        }
    };

    openDetailsModal() {
        ModalService.open("offer_proposition_details_" + this.props.proposition.code);
    };

    render() {
          const proposition = this.props.proposition;
          const linkName = this.props.linkName;

        return (
            <a href="#" className="u-inline-block u-spacing-right" onClick={() => this.openDetailsModal()}>
                {linkName}
            </a>
        )
    }
}

export default OfferPropositionDetails;
